import React, { useRef } from 'react';
import { NotificationManager } from 'react-notifications';
import { Link } from 'react-router-dom';
import axios from 'axios';
import './Register.css';

interface RegisterProps {
  login: string,
  password: string
}

const RegisterForm = (): JSX.Element => {
  const ref = useRef(null);

  const IsValidForm = (): boolean => {
    const form: HTMLFormElement = ref.current as HTMLFormElement;
    const username: string = form?.username?.value as string;
    const email: string = form?.email?.value as string;
    const password: string = form?.password?.value as string;
    const passwordRepeat: string = form?.passwordRepeat?.value as string;
    const approve: boolean = form?.approve?.checked as boolean;
    let isValid: boolean = true;

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (username.length === 0) {
      NotificationManager.warning('   ');
      isValid = false;
    }
    if (email.length === 0) {
      NotificationManager.warning('  e-mail');
      isValid = false;
    } else if (!emailRegex.test(email)) {
      NotificationManager.warning('  e-mail');
      isValid = false;
    }
    if (password.length === 0) {
      NotificationManager.warning('  ');
      isValid = false;
    } else if (password !== passwordRepeat) {
      NotificationManager.warning('  ');
      isValid = false;
    }

    if (!approve) {
      NotificationManager.error(
        '      '
      );
      isValid = false;
    }

    return isValid;
  };

  return (
    <form
      ref={ref}
      className="RegisterForm"
      onSubmit={(event) => {
        const form = event.target as HTMLFormElement;
        event.preventDefault();
        if (IsValidForm())
          RegisterUser({login: form.username.value, password: form.password.value})
          // NotificationManager.error('   ', '');
      }}
    >
      <h3 className="FormTitle"></h3>
      <input
        type="text"
        placeholder=" "
        className="FormInput"
        name="username"
      />
      <input
        type="text"
        inputMode="email"
        placeholder="E-mail"
        className="FormInput"
        name="email"
      />
      <input
        type="password"
        placeholder=""
        className="FormInput"
        name="password"
      />
      <input
        type="password"
        placeholder=" "
        className="FormInput"
        name="passwordRepeat"
      />
      <label className="FormCheckboxInput">
        <input type="checkbox" name="approve" />
            
      </label>

      <button type="submit" className="FormButton">
        
      </button>
      <span className="ToLogin">
         ?&nbsp;
        <Link to="/login">  </Link>
      </span>
    </form>
  );
};

async function RegisterUser(props: RegisterProps) {
  const url: string = '/accounts/create';

  const instance = axios.create({
    baseURL: "http://localhost:8081",
    headers: {
      'Content-Type': 'application/json',
      'Access-Control-Allow-Origin': 'http://localhost:3000'
    }
  });

  try {
    const response = await instance.post(url, props);
    const responseData = await response.data;
    console.log(responseData)
    return responseData;
  } catch (error) {
    console.error(error);
    throw error;
  }
}

export default RegisterForm;
